import React ,{useState, useEffect} from 'react';
import {Link} from 'react-router-dom';


export const MoreItem = () => {

    useEffect(()=>{
        newApi();
    },[])

    const [moreItems, setmoreItems] = useState([]);

    const newApi =async ()=> {
       const response = await  fetch('https://fakestoreapi.com/products?limit=5');
       const moreItems = await response.json();
       console.log(moreItems);
       setmoreItems(moreItems);
    }
    
    return (
        <div>
            <div style={{marginTop:"50px", fontFamily:"'Josefin Sans', sans-serif"}}><h2>More Items</h2></div>
            <div className="shop-card-container">

                {moreItems.map(item =>{
                    return(

                        
                        <div className="card" id={item.id}>
                            <div className="card_image"><img src={item.image} alt="" className="card-img"/></div>
                            <div className="card_content">
                            <h2 className="card_title">{item.title}</h2>
                            <p className="card_text">In: {item.category}</p>
                            {/* <button className="btn card_btn"> */}
                            <Link className="btn card_btn" id={item.id} to={`/shop/${item.id}`} style={{color: 'white',textDecoration: "none"} }>
                                Read More
                            </Link>
                            </div>
                        </div>
                )
                })}

                        <div className="card" id="more">
                            <div className="card_content more-content" >
                                <Link className="btn card_btn" id="moreLink" to={`/shop`} style={{color: 'black',textDecoration: "none"} }>
                                    More Items
                                </Link>
                            </div>
                        </div>

            </div>
                                    
        </div>
    )
}
