import React from 'react';
import { Link } from 'react-router-dom';


export const Footer = () => {
    return (
        <div>
                        
            {/* <!--  FOOTER START --> */}
            
            <div className="footer">
            <div className="inner-footer">

            {/* <!--  for company name and description --> */}
                <div className="footer-items">
                <h1>SHIVAM'S SHOP</h1>
                <p>Providing branded product with 100% security</p>
                </div>

            {/* <!--  for quick links  --> */}
                <div className="footer-items">
                <h3>Quick Links</h3>
                <div className="border1"></div>
                 {/* <!--for the underline --> */}
                    <ul>
                        <Link to="/"><li>Home</li></Link>
                        <Link to="/shop"><li>Shop</li></Link>
                        <Link to="/contact"><li>Contact</li></Link>
                        <Link to="/about"><li>About</li></Link>
                        

                    </ul>
                </div>

            {/* <!--  for some other links --> */}
                
            {/* <!--  for contact us info --> */}
                <div className="footer-items">
                <h3>Contact Developer</h3>
                <div className="border1"></div>
                    <ul>
                    <li><i className="fa fa-map-marker" aria-hidden="true"></i><a href="https://github.com/Iamshivam-dev">GITHUB</a></li>
                    <li><i className="fa fa-phone" aria-hidden="true"></i>yadavshivam1072@gmail.com</li>
                    <li><i className="fa fa-envelope" aria-hidden="true"></i></li>
                    </ul> 
                
                </div>
            </div>
            
            {/* <!--   Footer Bottom start  --> */}
            <div className="footer-bottom">
                Copyright &copy; SHIVAM 2021.
            </div>
            </div>
            
            {/* <!--   Footer Bottom end --> */}
            
            {/* <!--   FOOTER END --> */}
        </div>
    )
}
