import React from 'react';
import { Link } from 'react-router-dom';
import {Header} from './Header';

export const Home = () => {
    return (
        <div>
            <Header />
            <div className="bodyContent">
                <div className="body-card">
                    <div className="img-container">
                        <img src="./myImages/secure.png" alt="" />
                    </div>
                    <div className="card-content">
                        <h3>100% Secure</h3>
                        <p>
                            Buy Products from our site made by Security experts
                        </p>
                    </div>
                </div>
                
                <div className="body-card">
                    <div className="img-container">
                        <img src="./myImages/cashon.png" alt="" />
                    </div>
                    <div className="card-content">
                        <h3>Cash on Delivery</h3>
                        <p>
                            Pay Cash on Delivery when you get the product
                        </p>
                    </div>
                </div>
                
                <div className="body-card">
                    <div className="img-container">
                        <img src="./myImages/return.png" alt="" />
                    </div>
                    <div className="card-content">
                        <h3>Easy Return</h3>
                        <p>
                            Return back with easy Return Policies
                        </p>
                    </div>
                </div>

            </div>
            <Link to="/shop" style={{textDecoration: 'none'}}>
                <div className="shop-now">
                    <h1>
                        Shop Now &gt;

                    </h1>
                </div>

            </Link>
        </div>
    )
}
