import React, {useEffect, useState}from 'react';
import {MoreItem} from './MoreItem';


export const Item = ({match}) => {
    useEffect(()=>{
        fetchSingle();
    },[])
    const [item, setItem] = useState([]);

    const fetchSingle = async ()=>{
        const singleItem = await fetch(`https://fakestoreapi.com/products/${match.params.id}`)
        const item = await singleItem.json();
        setItem(item);
    }


    return (
        <>
        <div className="item-landing-page">
            <div className="left">
                <div className="item-img-container">
                    <img src={item.image} />
                </div>
                <div className="item-buttons">
                    <a className="item-button add-to-cart" href="">Add to cart</a>
                    <a className="item-button buy-now" href="">Buy Now</a>
                </div>
            </div>
            <div className="right">
                <h2 className="right-title">{item.title}</h2>
                <p className="branded">Branded</p>
                <p className="price-contaner">
                    Price: ${item.price} only
                    <span className="discount">
                        ${(item.price + (item.price/4))}
                    </span>
                    <span className="total-discount">
                        20% off
                    </span>
                </p>
                <p className="right-description">{item.description}</p>
                <p>Category: {item.category}</p>

            </div>
        </div>
        <MoreItem />

        </>
    )
}
